/* css enchere */
body {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    color: #333;
  }
  
  h1, h2, h3 {
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
  }
  
  h1 {
    font-size: 36px;
  }
  
  h2 {
    font-size: 24px;
  }
  
  h3 {
    font-size: 18px;
  }
  
  a {
    color: #0077c9;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  header {
    background-color: #0077c9;
    color: #fff;
    padding: 20px;
  }
  
  header a {
    color: #fff;
  }
  
  header a:hover {
    text-decoration: none;
  }
  
  .card {
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      transition: 0.3s;
      border-radius: 5px;
      overflow: hidden;
      margin: 20px 0;
  }
  
  .card img {
      width: 100%;
  }
  
  .card-text {
      padding: 2px 16px;
  }
  
  .card:hover {
      box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

  .interenchere-navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 2rem;
}
.interenchere-navbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
}
.interenchere-navbar-links {
    display: flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
}
.interenchere-navbar-links a {
    margin-left: 1rem;
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
}
@media (max-width: 600px) {
    .interenchere-navbar-links {
        display: none;
    }
    .interenchere-navbar-links.open {
        display: flex;
        flex-wrap: wrap;
    }
    .interenchere-navbar-links.open li {
        width: 100%;
        text-align: center;
    }
}
.buttonreq {
  /* default button styles */
  padding: 10px 20px; 
  margin-left: 20px;
  border-radius: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.buttonreq:hover {
  /* styles for when the button is hovered over */
  background-color: #3e8e41;
}
