/*css for login and inscription*/
body {
    font-family: 'Roboto', sans-serif;
    background-color: #f5f5f5;
  }
  
  .container {
    max-width: 360px;
    margin: 80px auto;
    padding: 20px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }
  
  h1, h2 {
    text-align: center;
    font-weight: 500;
  }
  
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  form {
    margin-top: 30px;
  }
  
  label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
  }
    
  input[type="text"], input[type="password"] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  input{
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  input[type="email"], input[type="password"] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  input[type="submit"] {
    width: 100%;
    background-color: #4285f4;
    color: #fff;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  input[type="submit"]:hover {
    background-color: #357ae8;
  }
  
  .form-links {
    text-align: center;
    margin-top: 20px;
  }
  
  .form-links a {
    color: #666;
    text-decoration: none;
  }
  
  .form-links a:hover {
    text-decoration: underline;
  }